.main-reader {
   padding: 0 1rem;

   .nav {
      display: flex;
      padding: 1rem;
      width: 90%;
      margin: 0 auto;
      align-items: center;
      justify-content: center;
      // gap: 10px;

      button,
      div {
         padding: 0.5rem;
         display: flex;
         align-items: center;
         justify-content: center;
         text-align: center;
         height: 35px;
         width: 35px;

         &[disabled] {
            color: #444;
            cursor: default;
         }

         a {
            height: 100%;
            width: 100%;
         }

         &.current {
            @include bgtext($x-secondary);
            cursor: default;

            &:hover {
               @include bgtext($x-secondary);
            }
         }
      }
   }

   &-box {
      position: relative;
   }

   &-flex {
      display: none;
      width: 90%;
      margin: 0 auto;
      grid-gap: 1rem;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      transition: 0.3s;

      &.current {
         display: grid;
      }

      .flex-item {
         max-width: 200px;
         background: #667;
         border-radius: 10px;
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         transition: 0.3s;

         .bg-img {
            height: 270px;
            z-index: 0;
            background: #000;
            border-top-right-radius: 10px;
            border-top-left-radius: 10px;
            object-fit: cover;
            transition: 0.3s;
            z-index: 9;
         }

         .description {
            padding: 0.5rem;
            z-index: 10;
            font-size: 13px;
            background: darken($x-primary, 10);
            border-bottom-right-radius: 10px;
            border-bottom-left-radius: 10px;

            p {
               text-align: center;
               white-space: nowrap;
               overflow: hidden;
               text-overflow: ellipsis;
            }
         }
      }
   }
}

.search {
   &-loading {
      padding: 0.5rem 0.2rem;
   }
   &-bar {
      width: 86%;
      margin: 0 auto;
      padding: 0.4rem 1rem;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      position: relative;

      input {
         padding: 0.5rem;
         width: 35%;
         background: transparent;
         border: none;
         font-size: 1rem;
         font-family: $font;
         transition: background-color 0.3s;
         border-radius: 10px;
         caret-color: $x-light;

         &:focus {
            background-color: lighten($x-dark, 5);
         }
      }
   }

   &-list {
      background: lighten($x-dark, 5);
      position: absolute;
      top: 100%;
      padding: 0.5rem 0.6rem;
      font-size: 0.9rem;
      z-index: 9999;
      width: 400px;
      border-radius: 10px;
      max-height: 500px;
      overflow-y: auto;
      overflow-x: hidden;
      display: grid;
      grid-template-columns: 1fr;
      gap: 15px;
   }

   &-item {
      // font-size: 0.8rem;
      padding: 0.5rem 0.2rem;
      display: flex;
      height: 50px;
      gap: 10px;
      justify-content: flex-start;
      align-items: center;

      .cover-img {
         background-size: cover;
         background-position: center;
         width: 3rem;
         height: 4rem;
         background-color: #444;
      }

      .name {
         display: block;
         font-size: 1rem;
         // letter-spacing: 20px;
         flex-grow: 1;

         p {
            text-align: left;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
         }
         // max-width: 500px;
         // overflow-x: hidden;
         // white-space: normal;
      }
   }
}
